.MainNav {
  background-color: #3c6262;
}

.MainNav__list {
  position: relative;
}

.MainNav__item {
  padding: 10px 0;
  color: white;
}

.MainNav__item a {
  position: relative;
  color: white;
}

.MainNav__count {
  position: absolute;
  right: -4px;
  top: 5px;
}

.MainNav__item_currency {
  padding: 0;
  position: absolute;
  top: 0;
  right: 15px;
  cursor: pointer;
}

.MainNav__currency {
  padding: 10px 0;
  position: relative;
}

.MainNav__currency ul {
  position: absolute;
  top: 100%;
  left: 50%;
  z-index: 2;
  transform: translateX(-50%);
  background-color: #3c6262;
}

.MainNav__currency li {
  padding: 10px;
}

.MainNav__currency li:hover {
  background-color: rgba(255, 255, 255, 0.1);
}
